html, body {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */

body {
  line-height: 1; }

* {
  box-sizing: border-box; }

html {
  font-size: 16px;
  font-family: 'Helvetica Neue', sans-serif; }

