        .form__field--has-resizing-placeholder .form__field-label.is-focus {
          top: 5px;
          transform: translateY(0) scale(0.7) perspective(1px); }
        .form__field--has-checkbox.is-checked .form__field--has-resizing-placeholder .form__field-label:before {
          content: "\f046"; }
        .form__field--has-radio-btn.is-checked .form__field--has-resizing-placeholder .form__field-label:before {
          content: "\f05d"; }

