  .modal.is-visible {
    visibility: visible;
    opacity: 1;
    transform: translateX(-50%) scale(1);
    transition: visibility 0.5s, opacity 0.5s, transform 0.5s cubic-bezier(0.215, 0.61, 0.355, 1) 0.1s; }

