    .navbar__hamburger.is-opened .navbar__hamburger-line:nth-child(1) {
      top: 50%;
      left: 50%;
      width: 0; }
    .navbar__hamburger.is-opened .navbar__hamburger-line:nth-child(2) {
      transform: rotate(45deg); }
    .navbar__hamburger.is-opened .navbar__hamburger-line:nth-child(3) {
      transform: rotate(-45deg); }
    .navbar__hamburger.is-opened .navbar__hamburger-line:nth-child(4) {
      bottom: 50%;
      left: 50%;
      width: 0; }

